  --color-grey-0: #fff;
  --color-system-error: #cd3e13;
  --color-system-blue: #09a0c1;
  --color-system-green: #008625;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: IBM Plex Sans, Arial, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.5;
}

body {
  color: var(--color-grey-10);
  background-color: var(--color-grey-85);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: bold;
}

img, picture, video, iframe {
  max-width: 100%;
  height: auto;
  display: block;
}

ul, ol {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  font: inherit;
  background: none;
  border: none;
  padding: 0;
}

input, textarea, select, button {
  font: inherit;
  color: inherit;
}

textarea {
  resize: vertical;
}

select {
  appearance: none;
}

.button {
  text-align: center;
  cursor: pointer;
  background-color: var(--color-primary-dark90);
  padding: 20px 46px;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
  display: inline-block;
}

.button:hover {
  background-color: var(--color-primary-dark80);
}

.button:focus {
  background-color: var(--color-primary-dark100);
}

.button--small {
  padding: 16px 32px;
  font-size: 18px;
}

.button--large {
  padding: 24px 64px;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 112px;
}

.section-title {
  letter-spacing: 2px;
  text-transform: uppercase;
  letter-spacing: 5px;
  color: var(--color-primary-dark70);
  width: 40%;
  font-family: IBM Plex Sans, sans-serif;
  font-size: 40px;
  font-style: normal;
  font-weight: 600;
  line-height: 110%;
}

.section-title--white {
  letter-spacing: 3.36px;
  color: var(--color-grey-10);
  font-family: Tektur, sans-serif;
  font-size: 56px;
  font-style: normal;
  font-weight: 500;
  line-height: 92%;
}

.header {
  background-color: var(--color-grey-85);
  padding: 28px 0;
}

.header__container {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  flex-shrink: 0;
  margin-right: 60px;
  padding: 12px 32px 12px 0;
}

.header__nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__menu {
  flex-flow: wrap;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__link {
  color: var(--color-grey-10);
  padding: 24px;
  font-size: 20px;
  font-weight: 500;
  line-height: 140%;
  transition: color .3s, background-color .3s;
  position: relative;
}

.header__link:after {
  content: "";
  background-color: #0000;
  height: 2px;
  transition: background-color .3s;
  position: absolute;
  bottom: 14px;
  left: 24px;
  right: 24px;
}

.header__link:hover:after {
  background-color: var(--color-primary-dark70);
}

.header__link:focus:after, .header__link--selected:after {
  background-color: var(--color-primary-light100);
}

.header__language-container {
  padding: 20px;
}

.header__call-button {
  margin-left: 60px;
}

.hero {
  background-color: var(--color-grey-100);
  padding: 160px 0;
}

.hero__container {
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.hero__content {
  width: 80%;
}

.hero__logo {
  width: 60%;
  margin: auto;
}

.hero__title, .hero__description {
  margin-bottom: 80px;
}

.services {
  padding: 160px 0;
}

.services__title {
  margin-bottom: 80px;
}

.services__grid-container {
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  display: grid;
}

.services__card {
  background-color: var(--color-grey-80);
  border-radius: 8px;
  padding: 40px;
}
